import card from '@/components/common/card.vue';
import divider from '@/components/common/divider.vue';
import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue';
import { mapState } from 'vuex';
export default {
	components: {
		card,
		divider,
		uniListItem
	},
	data() {
		return {
			orders: [
				{
					icon: 'icon-yinhangka',
					text: '待付款'
				},
				{
					icon: 'icon-huoche',
					text: '待收货'
				},
				{
					icon: 'icon-xiaoxi1',
					text: '待评价'
				},
				{
					icon: 'icon-editor2',
					text: '退换修'
				}
			],
			listNav: [
				{
					leftIcon: 'icon-huiyuan2',
					leftIconStyle: 'color:#fdc02e;',
					title: '小米会员',
					path: ''
				},
				{
					leftIcon: 'icon-huiyuanzhongxin',
					leftIconStyle: 'color:#fdc02e;',
					title: '会员中心'
				},
				{
					leftIcon: 'icon-aixin1',
					leftIconStyle: 'color:#fa6c5e;',
					title: '服务中心'
				},
				{
					leftIcon: 'icon-shouye1',
					leftIconStyle: 'color:#fd8b42;',
					title: '小米之家'
				},
				{
					leftIcon: 'icon-gengduo2',
					leftIconStyle: 'color:#9ed45a;',
					title: '更多功能'
				}
			]
		};
	},
	computed: {
		...mapState({
			loginStatus: state => state.user.loginStatus,
			userInfo: state => state.user.userInfo
		})
	},
	mounted() {
		console.log(this.loginStatus)
	},
	methods: {
		navigate(path, check = false) {
			if (!path) return;
			if (check) {
				return this.navigateTo({
					url: `/pages/${path}/${path}`
				});
			}
			uni.navigateTo({
				url: `/pages/${path}/${path}`
			});
		},
		openLogin() {
			uni.navigateTo({
				url: '../login/login'
			});
		}
	}
};
